export const initialState = {
    count1: 0,
    count2: 0
};

export const reducer = (state, action) => {
    const { type, counterType } = action;
    switch (type) {
        case "increment":
            return { ...state, [counterType]: state[counterType] + 1 };
        case "decrement":
            return { ...state, [counterType]: state[counterType] - 1 };
        case "reset":
            return { ...state, [counterType]: initialState[counterType] };
        default:
            return state;
    };
};