import React, { useEffect, useRef, useState } from 'react'
import Button from "../Button/Button";
import { Label } from "reactstrap";
import useDocumentTitle from '../../hooks/useDocumentTitle';

function Counter({ counter, count, dispatch, number1 }) {
    const IntervalRef = useRef(null);
    const [timer, setTimer] = useState(0);
    useDocumentTitle({ type: counter, counts: count });

    useEffect(() => {
        IntervalRef.current = setInterval(() => {
            setTimer(prevTimer => prevTimer + 1);
        }, 1000);
        console.log("Counter component is mount");
        return () => {
            clearInterval(IntervalRef.current);
            console.log("Counter Component Unmount")
        }
    }, [])

    return (
        <div className="cards m-1">
            <Label>{number1}</Label>
            <br />
            <div className="d-flex align-items-center flex-column shadow p-5 bg-white rounded">
                <span className="mb-3 border p-2">Timer : {timer}
                    <Button color="danger" onClick={() => clearInterval(IntervalRef.current)} label="Stop" className="float-right mx-3 py-0" />
                </span>
                <h3 className="mb-5 text-dark font-weight-bolder">Counts : {count}</h3>
                <div className="d-flex justify-content-between w-100">
                    <Button
                        color="primary"
                        onClick={() => dispatch({ type: "increment", counterType: counter })}
                        label="+" />
                    <Button
                        color="warning"
                        onClick={() => dispatch({ type: "decrement", counterType: counter })}
                        disabled={count === 0}
                        label="-" />
                    <Button
                        color="danger"
                        onClick={() => dispatch({ type: "reset", counterType: counter })}
                        disabled={count === 0}
                        label="Reset" />
                </div>
            </div>
        </div>
    )
};


export default Counter;