import React from "react";
import { Button } from "reactstrap";

const ButtonComponent = (props) => {
    const { color, onClick, disabled, label, className } = props;
    return (
        <Button
            className={"font-weight-bolder d-flex align-items-center shadow-none " + className}
            size="sm"
            color={color}
            outline
            onClick={onClick}
            disabled={disabled}>{label}</Button>
    );
};

export default ButtonComponent;