import React, { useReducer, useMemo, useCallback } from "react";
import Counter from "../../Component/Counter/Counter";
import { reducer, initialState } from "../../Reducer/CounterReducer";
import FormData from "../../Component/FormData";
import { Label } from "reactstrap";
const CounterPage = () => {
    const [counter, dispatch] = useReducer(reducer, initialState);

    const randomNumber1 = useMemo(() => Math.floor(Math.random() * counter.count1), [counter.count1]);
    const randomNumber2 = useMemo(() => Math.floor(Math.random() * counter.count2), [counter.count2]);
    const randomNumber3 = useCallback(() => { return Math.random() }, []);

    return (
        <div className="vh-100 my-5 d-flex flex-wrap align-items-center justify-content-center">
            {/* <Label>{Math.random()}</Label> */}
            <Counter
                counter="count1"
                // number1={randomNumber1}
                count={counter.count1}
                dispatch={dispatch} />
            <Counter
                counter="count2"
                // number1={randomNumber2}
                count={counter.count2}
                dispatch={dispatch} />
            <FormData number={randomNumber3} />
        </div>
    );
};
export default React.memo(CounterPage);