import React from "react";
import { Label, Button, Input } from "reactstrap";
import useInput from "../hooks/useInput";

function FormData({ number }) {
    const [firstName, firstNameAttributes, resetFirstName] = useInput('');
    const [lastName, lastNameAttributes, resetLastName] = useInput('');

    const handleSubmit = () => {
        alert(`Hello ${firstName} ${lastName} `);
        resetFirstName();
        resetLastName();
    }
    return (
        <div className="cards" >
            {/* <Label>{number()}</Label> */}
            <div className="d-flex flex-column m-1 shadow py-2 px-4 bg-white rounded">
                <Label className="my-3">
                    FirstName : <Input {...firstNameAttributes} type="text" />
                </Label>
                <Label className="my-2">
                    LastName : <Input {...lastNameAttributes} type="text" />
                </Label>
                <Label className="my-1 heading w-100">{firstName} - {lastName}</Label>
                <Button onClick={handleSubmit} className="my-2" disabled={firstName.trim() === "" || lastName.trim() === ""}>Submit</Button>
            </div>
        </div>
    );
};

export default React.memo(FormData);